<template>
    <div class="hot">
        <div class="header">
            <div class="logo">
                <img
                    class='logo-img'
                    :src="userIcon">
                <div class="designation"/>
            </div>
            <div class="title">
                <div class="name">专属今日头条</div>
                <div class="desc">{{ headerCard }}</div>
            </div>
            <div class="calendar">
                <div class="day">
                    {{ date.day }}
                </div>
                <div class="solar">{{ date.solar }}</div>
                <div class="lunar">{{ date.lunar }}</div>
                <div class="year">{{ date.year }}
                    <span class='animail'>{{ date.animal }}</span>
                </div>
            </div>
        </div>

        <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="getNews">
            <card
                v-for="item in list"
                :key="item.millisAtStartOfDay"
                :title="item.title"
                :documents="item.documentList"
                :limit="item.foldCount" />
        </van-list>
        <div class="van-more"/>
        <div
            class="footer"
            @click='toMakeMyCard' >
            <div class="footer-btn">
                点击制作
            </div>
        </div>
    </div>
</template>

<script>
import { getTabLink } from '@/utils/jsApiV2/deeplink';
import { setShareInfo, deeplinkApp } from '@/utils/share';
import { getHotNewsByTime, getHotCard } from '@/api/hot';
import Card from './Card';
import { defaultHeaderCard, footerlink, defaultUserImg, cKey } from './config';

export default {
    name: 'Hot',
    components: {
        Card,
    },
    data() {
        return {
            list: [],
            millisAtStartOfDay: null,
            loading: false,
            finished: false,
            userIcon: '',
            headerCard: defaultHeaderCard,
            date: {
                day: '',
                solar: '',
                lunar: '',
                year: '',
                animal: '',
            },
        };
    },
    created() {
        this.$root.setBackflow();
        this.getHotCardContent();
        this.$o2o({ e_c: '专属头条页', e_a: '曝光', e_n: '热点资讯专属头条' });
    },
    methods: {
        getHotCardContent() {
            const data = {
                sharedBy: this.$route.query.sharedBy,
            };
            getHotCard(data).then((res) => {
                this.userIcon = (res.userInfo && res.userInfo.headIcon) || defaultUserImg;
                this.headerCard = res.greet;
                this.date = {
                    day: res.date.day,
                    solar: `${res.date.month}月${res.date.day}日`,
                    lunar: `${res.lunar.month}${res.lunar.dayOfMonth}`,
                    year: res.lunar.year,
                    animal: res.lunar.animal,
                };
            }).catch(() => {
                this.$error();
            });
        },
        getNews() {
            const startTime = this.getStartTimestamp();
            getHotNewsByTime({
                startTime,
            })
                .then((res) => {
                    this.loading = false;
                    this.finished = !res.hasMore;

                    this.list = [...this.list, ...res.newsList];
                    this.millisAtStartOfDay =
                        (res.newsList[res.newsList.length - 1] || {}).millisAtStartOfDay;

                    if (!startTime) {
                        const firstNews = this.list[0].documentList[0] || {};
                        const secondNews = this.list[0].documentList[1] || {};

                        setShareInfo({
                            title: '今日热点资讯精选',
                            summary: `${firstNews.title};${secondNews.title}`,
                            pic: firstNews.images ? firstNews.images[0] : '',
                            url: location.href,
                        });
                    }

                    this.$root.hidePlaceholder();
                })
                .catch(() => {
                    this.$error(this.$route.name);
                });
        },
        getStartTimestamp() {
            if (!this.millisAtStartOfDay) return null;

            const day = new Date(this.millisAtStartOfDay);
            day.setDate(day.getDate() - 1);

            return day.getTime();
        },
        toMakeMyCard() {
            this.$o2o({ e_c: '制作按钮', e_a: '点击', e_n: '制作我的专属头条' });
            deeplinkApp(getTabLink(footerlink), undefined, cKey);
        },
    },
};
</script>

