<template>
    <component
        :is="itemStyleMap[itemStyle]"
        :item="item"
        v-on="$listeners" />
</template>

<script>
import TextItem from './TextItem';
import SingleImgItem from './SingleImgItem';
import LargeImgItem from './LargeImgItem';
import ThreeImgItem from './ThreeImgItem';
import VideoInlineItem from './VideoInlineItem';

export default {
    name: 'NewsItem',
    components: {
        TextItem,
        SingleImgItem,
        LargeImgItem,
        ThreeImgItem,
        VideoInlineItem,
    },
    props: {
        item: {
            type: Object,
            default: () => ({}),
        },
    },
    computed: {
        itemStyle() {
            return this.item.style === '0' ? 0 : 1;
        },
    },
    data() {
        return {
            itemStyleMap: {
                0: 'TextItem',
                1: 'SingleImgItem',
                2: 'LargeImgItem',
                3: 'ThreeImgItem',
                inline_video_item: 'videoInlineItem',
                short_video_item: 'videoInlineItem',
                // news_atlas_item: 'NewsAtlasItem',
                // news_atlas_large_item: 'NewsAtlasLargeItem',
                // news_atlas_three_item: 'NewsAtlasThreeItem',
            },
        };
    },
};
</script>
