<template>
    <div
        v-if="documents.length > 0"
        class="card border-top">
        <div class="content">
            <div class="title">
                <div class="logo" />
                <div class="name">{{ title }}</div>
            </div>
            <div class="news-list">
                <news-item
                    class="news-item"
                    v-for="item in list"
                    :key="item.docid"
                    :item="item"
                    @click="handleClickNewsItem(item)" />
            </div>
            <div
                class="more"
                v-if="hasMore && !isShowAll">
                <div
                    class="more-btn"
                    @click="showMore">查看更多</div>
            </div>
        </div>
    </div>
</template>

<script>
import NewsItem from '@/components/NewsItem';
import { deeplinkApp } from '@/utils/share';
import { getDetailDeeplink } from '@/utils/jsApiV2/deeplink';
import { set, get, remove } from '@/utils/cache';
import { cacheReadNewsCountKey } from './config';

export default {
    name: 'HotCard',
    components: {
        NewsItem,
    },
    props: {
        title: {
            type: String,
            default: '今日热点',
        },
        documents: {
            type: Array,
            default: () => [],
        },
        limit: {
            type: Number,
            default: 3,
        },
    },
    data() {
        return {
            isShowAll: false,
            limitCount: 3,
        };
    },
    computed: {
        list() {
            if (this.isShowAll) {
                return this.documents;
            }

            return this.limit ? this.documents.slice(0, this.limit) : this.documents;
        },
        count() {
            return this.documents.length;
        },
        hasMore() {
            return this.count > this.limit;
        },
    },
    methods: {
        showMore() {
            this.isShowAll = true;
        },
        handleClickNewsItem(item) {
            const countInfo = JSON.parse(get(cacheReadNewsCountKey) || '{}');
            const count = countInfo.count || 0;
            const now = new Date();
            const countTime = new Date(countInfo.time);
            const isSameDay = this.isSameDay(now, countTime);

            if (!isSameDay) {
                remove(cacheReadNewsCountKey);
            }

            if (count >= this.limitCount || item.type === 'video') {
                deeplinkApp(getDetailDeeplink(item.type, item.docId));
            } else {
                set(cacheReadNewsCountKey, JSON.stringify({
                    count: count + 1,
                    time: now,
                }));

                location.href = `${item.shareUrl}&backflow=0`;
            }
        },
        isSameDay(d1, d2) {
            return d1.getFullYear() === d2.getFullYear()
            && d1.getMonth() === d2.getMonth()
            && d1.getDate() === d2.getDate();
        },
    },
};
</script>

