import config from '@/config';
import { openDeeplink, tryCall } from './index';

// 想看deeplink的跳转    https://wiki.n.miui.com/pages/viewpage.action?pageId=111550986

/**
 *
 * 打开deeplink，主要是广告使用，广告里面一般也会有他们自己的deeplink
 * @export
 * @param {*} { deepLink }
 * @returns
 */
export function openDeepLink({ deepLink }) {
    return tryCall('openDeepLink', deepLink);
}

// 低版本使用的跳转tab的链接，新版本建议使用下面的goIndexTab
export function goTab(tab) {
    return openDeeplink(`web.tab/${tab}`);
}

// 首页tab跳转
export function goIndexTab(tab) { // newsTab,videoTab,topicTab,mineTab,chatTab,permissionSetting
    return openDeeplink(`goto/${tab}`);
}

// 详情页跳转 发布器
export function goPublishTopic(topicId, topicName) {
    return openDeeplink(`goto/publishTopic?docId=${topicId}&selectTopic=${topicName}&pageStyle=1`);
}

// 个人主页
export function goUserProfile({ headIcon = config.default.userIcon, nickname, userId }) {
    return openDeeplink(`goto/userInfoDetail?userId=${userId}&headIcon=${encodeURIComponent(headIcon)}&nickName=${nickname}`);
}

// 打开网页
export function getWebviewDeeplink(url) {
    return `bikan://web.goto/commonWebView?url_encode=${encodeURIComponent(url)}`;
}

// 评论详情页
export function goTopicCommentDetail({ topicId, reviewId, topicTitle }) {
    const url = 'goto/commentDetail?topicStyle=comment&cate=topic&from_type=1&source=topic';

    return openDeeplink(`${url}&docId=${encodeURIComponent(topicId)}&showTopicId=${encodeURIComponent(topicId)}&reviewId=${encodeURIComponent(reviewId)}&topicTitle=${encodeURIComponent(topicTitle)}&title=${encodeURIComponent(topicTitle)}`);
}

export function gotoAssignUrl(url) {
    location.href = url;
}

/*
    Todo:  goTopicCommentDetail与getTopicCommentDetailDeeplink 存在重复
    两个方法都有引用
*/
export function getTopicCommentDetailDeeplink({ topicId, reviewId, topicTitle }) {
    const url = 'bikan://goto/commentDetail?topicStyle=comment&cate=topic&from_type=1&source=topic';

    return `${url}&docId=${encodeURIComponent(topicId)}&showTopicId=${encodeURIComponent(topicId)}&reviewId=${encodeURIComponent(reviewId)}&topicTitle=${encodeURIComponent(topicTitle)}&title=${encodeURIComponent(topicTitle)}`;
}

//  未找到相关定义
export function getTabLink(tab) {
    return `bikan://goto/${tab}`;
}

export function goMineTabTask() {
    const url = 'goto/mineTab/task';
    return openDeeplink(url);
}


/*
    Todo:  goDetail方法无引用，且与getDetailDeeplink 存在重复
*/
export function goDetail(type, docId) {
    return openDeeplink(`goto/${type}Detail?docId=${docId}`);
}

export function goTopicDetail(topicId) {
    return openDeeplink(`goto/topicDetail?topicId=${topicId}`);
}

export function getTopicDetailDeeplink(topicId) {
    return `bikan://goto/topicDetail?topicId=${topicId}`;
}

export function getDetailDeeplink(type, docId) {
    return `bikan://goto/${type}Detail?docId=${docId}`;
}
